<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Nuevo Tipo de Financiamiento
        </h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="8"
      >
        <form @submit.prevent="guardarFinanciamiento()">
          <v-text-field
            v-model="financing.type"
            label="Tipo de Financiamiento"
            outlined
            prepend-icon="mdi-account-tie"
            required
          />
          <v-text-field
            v-model="financing.description"
            label="Descripción"
            outlined
            prepend-icon="mdi-label"
            required
          />
          <v-text-field
            v-model="financing.entity"
            label="Entidad Financiera"
            outlined
            prepend-icon="mdi-lock"
            required
          />
          <v-text-field
            v-model="financing.support"
            label="Sustento"
            outlined
            prepend-icon="mdi-lock"
            required
          />
          <v-text-field
            v-model="financing.office"
            label="Oficina"
            prepend-icon="mdi-cash"
            outlined
            required
          />
          <v-text-field
            v-model="financing.account_executive"
            label="Sectorista"
            prepend-icon="mdi-account"
            outlined
            required
          />

          <v-card-actions>
            <v-btn
              to="/ventas/financiamiento"
              color="blue-grey"
              class="mr-4"
            >
              Cancelar
            </v-btn>
            <v-btn
              type="submit"
              color="green"
              class="mr-4"
            >
              Guardar
            </v-btn>
          </v-card-actions>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
  import axios from 'axios'

  const urlFinanciamiento = 'https://sistema-adv.herokuapp.com/api/financing/'

  export default {
    name: 'CrearFinanciamiento',
    data () {
      return {
        financing: {
          type: '',
          description: '',
          entity: '',
          support: '',
          office: '',
          account_executive: '',
        },
      }
    },
    mounted () {},
    methods: {
      guardarFinanciamiento () {
        const router = this.$router
        const params = this.financing
        axios.post(urlFinanciamiento, params).then((res) => {
          // console.log(res);
          router.push('/ventas/financiamiento')
          this.limpiarCampos()
        })
      },
      limpiarCampos () {
        this.financing = ''
      },
    },
  }
</script>
